html,
body {
  overflow-x: hidden;
}

body {
  font-family: Ubuntu;
}

#root {
  width: 100%;
}

.nav-bar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 60px;
  background-color: #343a40;
  text-align: center;
  border-bottom: 3px #0199c7 solid;
  padding-left: 7%;
  z-index: 10;
}

nav.container {
  max-width: 100%;
}

.nav-bar ul {
  float: left;
}

ul a {
  color: #eaeeee;
  font-size: 20px;
}

ul .active {
  color: #0288b1;
  font-weight: bolder;
  font-size: 20px;
}

.nav-item:hover {
  color: #629296;
  cursor: pointer; 
}

#content-container {
  margin-top: 65px;
}

.profile-container {
  text-decoration: none;
  margin-top: 85px;
  padding: 0;
  display: inline-block;
  padding: 20px;
}

.profile-front,
.profile-back {
  max-width: 300px;
  max-height: 300px;
  object-fit: cover;
  border-radius: 100%;
  backface-visibility: hidden;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1),
    0 6px 20px 0 rgba(143, 143, 143, 0.19);
}

.profile {
  transform-style: preserve-3d;
  transition: all 0.5s;
}

.profile-container:hover .profile {
  transform: rotateY(180deg);
}

.profile-back {
  transform: rotateY(180deg);
  position: absolute;
}

.about-me {
  margin-bottom: 30px;
}

#about-title {
  font-weight: 600;
  font-size: 35px;
}

.about-me-text {
  font-size: larger;
}

.description {
  margin-top: 70px;
}

.hello-im-dilan {
  font-weight: 600;
  font-size: 50px;
}

p {
  color: #243030;
  font-size: 30px;
}

p {
  margin: 15px;
}

.logos-list {
  float: right;
}

.logos {
  height: 3em;
}

.resume {
  background-color: orange;
}

#first-project {
  font-weight: 600;
  font-size: 35px;
}

.projects-font {
  margin: auto;
}

.projects {
  width: 100%;
  border-radius: 15px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1),
    0 6px 20px 0 rgba(143, 143, 143, 0.19);
}

.projects:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.project-names {
  font-weight: 600;
  font-size: 25px;
}

.project-descriptions {
  font-size: 17px;
}

.project-links {
  text-align: center;
}

.project-links a {
  color: #0288b1;
  font-size: 17px;
}

#information p {
  font-size: 20px;
}

#contact {
  font-weight: 600;
  font-size: 35px;
  color: #0199c7;
  padding-top: 10px;
  padding-bottom: 20px;
}

.contact-container {
  line-height: 1.2em;
  margin-top: 90px;
  margin-bottom: 0;
  padding-top: 20px;
  padding-bottom: 25px;
  padding-left: 13%;
  width: 100%;
  background-color: #42484f;
  text-align: left;
}

.contact-container p,
.contact-container a {
  color: #eaeeee;
}

#copyright {
  padding-top: 15px;
  font-size: 14px;
}
